<template>
	<view class="container">
		<!-- 顶部选择区域 -->
		
		<!-- 中部内容区域 -->
		<view class="content">
			<textarea v-model="promptTem.promptTemplate" class="template-input" placeholder="请输入记忆描述"
				:maxlength="-1" />
		</view>
		<!-- 底部保存按钮 -->
		<view class="footer">
			<button class="save-btn" :loading="isSaving" @tap="handleSave">
				保存配置
			</button>
		</view>
	</view>
</template>
<script>
	import {
		updateUserPromptTem,
		getPromptTem
	} from '@/api/report'
	import storage from '@/utils/storage'
	import constant from '@/utils/constant'
	export default {
		data() {
			return {
				userId: storage.get(constant.userId),
				corpId: storage.get(constant.corpId),
				username: storage.get(constant.openid),
	
				promptTem: {
					agentId: 'desc',
					isAllSel: false,
					promptTemplate: '',
					userId: '',
				},
				isSaving: false
			}
		},
		computed: {
			isValid() {
				return this.promptTem.promptTemplate.trim() !== ''
			}
		},
		onShow() {
			this.getPromptTem()
		},
		methods: {
			getPromptTem(){
				getPromptTem(this.userId, 2).then(res => {
					this.promptTem.promptTemplate = res.data
				})
			},
			handleSave() {
				// if (!this.isValid) return

				this.promptTem.userId = this.userId

				this.isSaving = true
				updateUserPromptTem(this.promptTem).then(res => {
					if(res.code == 201){
						uni.showToast({
							title: res.messages,
							icon: 'none',
							duration: 1500,
						});
					}else{
					uni.showToast({
						title: '记忆已保存',
						icon: 'success',
						duration: 1500,
					});	
					}
					
					this.isSaving = false
				})

			}
		}
	}
</script>
<style>
	page {
		height: 100%;
	}

	.container {
		display: flex;
		flex-direction: column;
		height: 100%;
		background-color: #f5f5f5;
	}

	.scroll-view {
		background-color: #ffffff;
		padding: 24rpx 0;
		flex-shrink: 0;
	}

	.header {
		padding: 0 24rpx;
		white-space: nowrap;
	}

	.select-btn {
		display: flex;
		align-items: center;
		justify-content: center;
		height: 64rpx;
		padding: 0 32rpx;
		font-size: 14px;
		color: #666666;
		background-color: #f5f5f5;
		border-radius: 32rpx;
		transition: all 0.3s;
	}

	.select-btn.active {
		color: #ffffff;
		background-color: #1ec57c;
	}

	.divider {
		width: 100%;
		height: 2rpx;
		background-color: #e5e5e5;
		margin: 20rpx 0;
	}

	.ai-list {
		display: grid;
		grid-template-columns: repeat(2, 1fr);
		gap: 20rpx;
	}

	.content {
		flex: 1;
		padding: 32rpx 24rpx;
		overflow: auto;
	}

	.title-row {
		display: flex;
		align-items: center;
		justify-content: space-between;
		margin-bottom: 24rpx;
	}

	.title {
		font-size: 16px;
		color: #333333;
		font-weight: 500;
	}

	.template-select {
		width: 380rpx;
	}

	.template-input {
		width: 100%;
		height: 400rpx;
		background-color: #ffffff;
		border-radius: 16rpx;
		padding: 24rpx;
		font-size: 14px;
		line-height: 1.5;
		color: #333333;
	}

	.footer {
		padding: 24rpx;
		background-color: #ffffff;
		flex-shrink: 0;
	}

	.save-btn {
		width: 100%;
		height: 88rpx;
		line-height: 88rpx;
		background-color: #1ec57c;
		color: #ffffff;
		font-size: 16px;
		border-radius: 44rpx;
	}

	.save-btn[disabled] {
		background-color: #cccccc;
		color: #ffffff;
	}
</style>